.VatViewer-loading {
  text-align: center;
}

.VatViewer-loading-logo {
  -webkit-animation: VatViewer-loading-spin infinite 10s linear;
          animation: VatViewer-loading-spin infinite 10s linear;
  height: 50px;
}

.VatViewer-check-result div {
  width: 50%;
  float: left;
}

@-webkit-keyframes VatViewer-loading-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes VatViewer-loading-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}



